@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.1/font/bootstrap-icons.css);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.1/font/bootstrap-icons.css);

.os-product-card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.13);
  border-radius: 8px;
  overflow: hidden;
  background-color: #fff;
  display: grid;
  align-content: space-between;
  grid-template-rows: 0.5fr 0.5fr 40px;
}

.os-product-img {
  display: flex;
  height: 200px;
  justify-content: center;
  margin: 12px 12px 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.os-product-content {
  padding: 22px 24px;
}

.os-product-rating {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1;
  max-width: 70px;
  padding: 6px 12px;
  border-radius: 3x;
  background-color: #7e72f2;
  color: #fff;
  font-size: 0.875rem;
  font-weight: 400;
}

.os-product-price-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.os-product-price {
  color: #2c2c2c;
  font-size: 1.125rem;
  font-weight: 400;
}

.os-product-description,
.os-product-title {
  margin: 0;
  font-weight: 400;
  color: #2c2c2c;
}

.os-product-title {
  font-size: 1rem;
  margin-bottom: 8px;
}

.os-product-description {
  font-size: 0.75rem;
}

.os-product-footer {
  display: grid;
  grid-template-columns: 1fr;
}

.os-product-footer button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 13px;
  padding-bottom: 13px;
  border-radius: 0;
}

.os-products-list {
  display: grid;
  grid-template-columns: 270px 270px 270px;
  grid-gap: 20px;
  margin: 40px 0;
}

.cart-container {
  padding: 10px;
}

.cart-list {
  padding: 0;
  margin: 0;
}

.item-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 15%);
  background: #fff;
  border-radius: 3px;
}

.item-preview {
  height: 40px;
  display: flex;
  align-items: center;
}

.item-preview img {
  width: 100%;
  max-height: 40px;
}

.item-counter,
.item-name,
.item-preview,
.item-price {
  padding: 0 10px;
}

.item-counter {
  min-width: 120px;
}

.item-price {
  min-width: 100px;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.order-btn {
  width: 200px;
}

.cart-total {
  padding: 10px;
}

.count-btn {
  background: 0 0;
  border: none;
  outline: 0;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.os-btn-default,
.os-btn-primary,
.os-btn-white {
  padding: 8px 12px;
  border: none;
  border-radius: 3px;
  font-size: 0.875rem;
  font-weight: 400;
  font-family: Montserrat, sans-serif;
  line-height: 1;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s;
}

.os-btn-primary {
  background-color: #7e72f2;
  color: #fff;
}

.os-btn-primary:hover {
  background-color: #6d5ff0;
}

.range-slider__inner {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 4px;
  margin: 10px 14px;
  background: #c8c2f3;
  box-shadow: 0 0 1px #7e72f2;
  user-select: none;
}

.range-slider__thumb-left,
.range-slider__thumb-right {
  position: absolute;
  top: 50%;
  margin-top: -9px;
  height: 18px;
  width: 6px;
  cursor: grab;
}

.range-slider_dragging,
.range-slider_dragging .range-slider__thumb-left,
.range-slider_dragging .range-slider__thumb-right {
  cursor: grabbing;
}

.range-slider__thumb-left {
  left: 0;
  margin-left: -6px;
}

.range-slider__thumb-right {
  right: 0;
  transform: rotate(180deg);
  margin-right: -6px;
}

.range-slider__progress {
  background: #7e72f2;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.range-slider {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.range-slider > span {
  font-size: 0.8rem;
  position: absolute;
  top: 100%;
}

.range-slider > span:first-child {
  left: 0;
}

.range-slider > span:last-child {
  right: 0;
}

.os-form-group {
  padding: 20px 10px;
  background-color: #fff;
}

.os-form-group.divider {
  border-bottom: 1px solid #d6d6d6;
}

.os-form-checkbox > input {
  display: none;
}

.os-form-checkbox > input:checked + label::before {
  border-color: #7e72f2;
  background-color: #7e72f2;
  content: "✓";
  color: #fff;
  text-align: center;
}

.os-form-checkbox > label {
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: 300;
  color: #2c2c2c;
  cursor: pointer;
}

.os-form-checkbox > label::before {
  content: "";
  flex-shrink: 0;
  display: inline-block;
  margin-right: 12px;
  border: 1px solid #979797;
  width: 20px;
  height: 17px;
  border-radius: 3px;
}

.os-filters-panel-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.os-filters-panel-item .os-form-checkbox {
  margin-bottom: 0;
}

.os-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0px 30px 0px;
}

.os-pagination a {
  line-height: 1;
  text-decoration: none;
}

.os-pagination .next,
.os-pagination .previous {
  color: #000;
}

.os-pagination .previous {
  margin-right: 15px;
}

.os-pagination .next {
  margin-left: 15px;
}

.os-pagination .page-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  border-radius: 20px;
}

.os-pagination .page-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #ededed;
  border-radius: 20px;
  color: #2c2c2c;
  transition: 0.3s;
}

.os-pagination .page-link.active,
.os-pagination .page-link:hover {
  background-color: #7e72f2;
  color: #fff;
}

.os-form-input > input {
  width: 100%;
  padding: 12px 20px;
  border-radius: 8px;
  border: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  font-size: 1.2rem;
  font-weight: 300;
  box-sizing: border-box;
  font-family: Montserrat, sans-serif;
}

.os-form-input > input:focus,
.os-form-input > input:focus-visible {
  border: none;
  outline: 0;
}

.os-form-input.use-icon {
  position: relative;
}

.os-form-input.use-icon > input {
  padding-right: 70px;
}

.os-form-input.use-icon .input-icon {
  display: flex;
  align-items: center;
  position: absolute;
  height: 100%;
  right: 40px;
  top: 0;
  font-size: 1.375rem;
  color: #606060;
}

.os-filters-panel-content {
  padding: 0 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 20px;
}

.clear-filters {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  font-size: 1rem;
}

.os-form-title {
  font-weight: 600;
  font-size: 1.2rem;
  margin-bottom: 18px;
}
