.os-product-card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.13);
  border-radius: 8px;
  overflow: hidden;
  background-color: #fff;
  display: grid;
  align-content: space-between;
  grid-template-rows: 0.5fr 0.5fr 40px;
}

.os-product-img {
  display: flex;
  height: 200px;
  justify-content: center;
  margin: 12px 12px 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.os-product-content {
  padding: 22px 24px;
}

.os-product-rating {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1;
  max-width: 70px;
  padding: 6px 12px;
  border-radius: 3x;
  background-color: #7e72f2;
  color: #fff;
  font-size: 0.875rem;
  font-weight: 400;
}

.os-product-price-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.os-product-price {
  color: #2c2c2c;
  font-size: 1.125rem;
  font-weight: 400;
}

.os-product-description,
.os-product-title {
  margin: 0;
  font-weight: 400;
  color: #2c2c2c;
}

.os-product-title {
  font-size: 1rem;
  margin-bottom: 8px;
}

.os-product-description {
  font-size: 0.75rem;
}

.os-product-footer {
  display: grid;
  grid-template-columns: 1fr;
}

.os-product-footer button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 13px;
  padding-bottom: 13px;
  border-radius: 0;
}

.os-products-list {
  display: grid;
  grid-template-columns: 270px 270px 270px;
  grid-gap: 20px;
  margin: 40px 0;
}

.cart-container {
  padding: 10px;
}

.cart-list {
  padding: 0;
  margin: 0;
}

.item-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 15%);
  background: #fff;
  border-radius: 3px;
}

.item-preview {
  height: 40px;
  display: flex;
  align-items: center;
}

.item-preview img {
  width: 100%;
